import React, { Component } from 'react';
import { Link } from "react-router-dom";


class Home extends Component {
  render() {
    
    return (
      <div className="Home">
        <main>
          <section className="top">
            <div className="welcomebox">
              <h2>The best way to observe a fish is to become a fish.</h2>
              <h3>Jacques Cousteau</h3>
            </div>
          </section>
          <section className="dykkerudstyr">
            <div className="lines">
              <div className="blackline">
                <h3>Mest Populær dykkerudstyr</h3>
              </div>
              <div className="greyline"></div>
            </div>
            <div className="products">
              <div className="container">
                <img src="./img/reg.jpg" className="img" alt=""/>
                  <h2>
                        Regulator med resist
                  </h2>
                  <p>kr. 899,-</p>
              </div>
              <div className="container">
                <img src="./img/fine.jpg" className="img" alt=""/>
                  <h2>
                        Rejsefinne
                  </h2>
                  <p>kr. 999,-</p>
              </div>  
              <div className="container">
                <img src="./img/fine.jpg" className="img" alt=""/>
                  <h2>
                        Rejsefinne
                  </h2>
                  <p>kr. 999,-</p>
              </div>          
              <div className="container">
                <img src="./img/hanske.jpg" className="img" alt=""/>
                  <h2>
                        coldwater gloves
                  </h2>
                  <p>kr. 1499,-</p>
              </div>         
            </div>
            <Link to="/dykkerudstyr" className="semerudstyr"><h3>Se mere lækkert udstyr her!</h3></Link>
          </section>
          <section className="dykudiverden">
          <div className="lines">
              <div className="blackline">
                <h3>Dyk ud i verden</h3>
              </div>
              <div className="greyline"></div>
            </div>
            <div className="flex">
            <div className="gcontainer">
              <div className="imgcontainer">
                <img src="/img/malta.jpg" alt=""/>
                </div>
            <div className="blackbox">
              <h2>Malta</h2>
              <p>Oplev skønne Malta og det helt fantastiske dyreliv og sammenhold. Vi arrangerer dykkerejser til Malta hele året rundt.</p>
            </div>
            </div>
            <div className="fillout">

            </div>
            <div className="gcontainer">
              <div className="imgcontainer">
                <img src="/img/norge.jpg" alt=""/>
                </div>
            <div className="blackbox">
              <h2>Norge</h2>
              <p>Oplev skønne Norge og det helt fantastiske dyreliv og sammenhold. Vi arrangerer dykkerejser til Norge hele året rundt.</p>
            </div>
            </div>
            </div>
            <Link to="/dykkerrejse" className="semerudstyr"><h3>Se flere destinationer og muligheder her!</h3></Link>
          </section>
          <section className="cerf">
          <div className="lines">
              <div className="blackline">
                <h3>Padi - dykkercertifikat</h3>
              </div>
              <div className="greyline"></div>
            </div>
            <div className="padibox">
              <article>
                <h3>Tag et internationalt anerkendt dykkercertifikat</h3>
                <p>her finder du et stort udvalg af dykkerkurser lige fra begynder til professionel. Vi er et af Danmarks førende dykkercentre,
                  som uddanner fra PADI organistationen. Når du vælger at tage et dykkercertifikat hos os, får du ikke kun et internationalt
                  anerkendt dykkercertifikat til markedets bedste priser, du får også et dykkerkursus, hvor kvaliteten er i top.
                </p>
                <br/>
                <p>
                  Vi har kompetente instruktører på vores dykkerkurser. Erfaring, tillid og sikkerhed er vigtigt, når du dykker med os, derfor står vi klar med et stort hold af kompetente instruktører
                </p>
              </article>
              <div className="padiboi">
                <img src="/img/PADI.png" alt=""/>
              </div>
            </div>
            <Link to="/certifikat" className="semerudstyr"><h3>Se alle kurser og cerifikater her!</h3></Link>
          </section>
        </main>
      </div>
    );
  }
}

export default Home;
