import React, { Component } from 'react';


class Certifikat extends Component {
  render() {
    return (
      <div className="Certifikat">
          <main>
          <section className="cerf">
          <div className="lines">
              <div className="blackline">
                <h3>Padi - dykkercertifikat</h3>
              </div>
              <div className="greyline"></div>
            </div>
            <div className="padibox">
              <article>
                <h3>Tag et internationalt anerkendt dykkercertifikat</h3>
                <p>her finder du et stort udvalg af dykkerkurser lige fra begynder til professionel. Vi er et af Danmarks førende dykkercentre,
                  som uddanner fra PADI organistationen. Når du vælger at tage et dykkercertifikat hos os, får du ikke kun et internationalt
                  anerkendt dykkercertifikat til markedets bedste priser, du får også et dykkerkursus, hvor kvaliteten er i top.
                </p>
                <br/>
                <p>
                  Vi har kompetente instruktører på vores dykkerkurser. Erfaring, tillid og sikkerhed er vigtigt, når du dykker med os, derfor står vi klar med et stort hold af kompetente instruktører
                </p>
              </article>
              <div className="padiboi">
                <img src="/img/PADI.png" alt=""/>
              </div>
            </div>
          </section>
          </main>
          <br/>
          <br/>
      </div>
    );
  }
}

export default Certifikat;