import React, { Component } from 'react';
import { NavLink, Link } from "react-router-dom";


class Header extends Component {
  render() {
    return (
      <div className="Header">
      <header>
        <div className="flexerino">
        <img src="img/ScubaLiving_logo_small.svg" id="img" alt=""/>
        <nav>
            <ul>
                <li><NavLink to="/">home</NavLink></li>
                <li><NavLink to="/dykkerudstyr">dykkeudstyr</NavLink></li>
                <li><NavLink to="/dykkerrejse">dykkerejse</NavLink></li>
                <li><NavLink to="/certifikat">dykkekurser</NavLink></li>
                <li><NavLink to="#">kontakt</NavLink></li>
                <li><NavLink to="#">om os</NavLink></li>
            </ul>
        </nav>
        <form action="send">
            <input type="text" name="search" id="search" />
            <button>søg</button>
        </form>
        <div id="login">
            <img src="img/lock-icon-300x300.png" id="img2" alt="" />
            <div id="logintxt"><Link to="#">Login</Link></div>
        </div>
      </div>
    </header>
        
      </div>
    );
  }
}

export default Header;