import React, { Component } from 'react';
import { BrowserRouter, Route, Switch } from "react-router-dom";
import Header from './header'
import Footer from './footer'
import Home from './Home/home'
import Dykkerudstyr from './Dykkerudstyr/Dykkerudstyr'
import Dykkerrejse from "./Dykkerrejse/Dykkerrejse"
import Certifikat from "./Certifikat/Certifikat"


class App extends Component {
  render() {
    return (
      <div className="App">
      <BrowserRouter>  
      <Header />
      <Switch>
      <Route exact path="/" component={Home} />
      <Route path="/dykkerudstyr" component={Dykkerudstyr} />
      <Route path="/dykkerrejse" component={Dykkerrejse} />
      <Route path="/certifikat" component={Certifikat} />
      </Switch>
      <Footer />
      </BrowserRouter>
      </div>
    );
  }
}

export default App;
