import React, { Component } from 'react';


class Dykkerrejse extends Component {
  render() {
    return (
        <div className="Dykkerrejse">
            <main>
                <section className="dykudiverden">
                    <div className="lines">
                        <div className="blackline">
                            <h3>Dyk ud i verden</h3>
                        </div>
                        <div className="greyline"></div>
                    </div>
                    <div className="flex">
                        <div className="gcontainer">
                            <div className="imgcontainer">
                                <img src="/img/malta.jpg" alt="" />
                            </div>
                            <div className="blackbox">
                                <h2>Malta</h2>
                                <p>Oplev skønne Malta og det helt fantastiske dyreliv og sammenhold. Vi arrangerer dykkerejser til Malta hele året rundt.</p>
                            </div>
                        </div>
                        <div className="fillout">

                        </div>
                        <div className="gcontainer">
                            <div className="imgcontainer">
                                <img src="/img/norge.jpg" alt="" />
                            </div>
                            <div className="blackbox">
                                <h2>Norge</h2>
                                <p>Oplev skønne Norge og det helt fantastiske dyreliv og sammenhold. Vi arrangerer dykkerejser til Norge hele året rundt.</p>
                            </div>
                        </div>
                    </div>
                    <br/>
                    <br/>
                </section>
            </main>
        </div>
        );
    }

}
    export default Dykkerrejse;